.colorNav {
  background: #2d343293;
}

section {
  text-align: center;
  margin: auto 10px auto 10px;
}

#main-title {
  display: block;
  font-size: 3em;
  font-weight: 800;
  font-family: sans-serif;
  margin: 0;
  margin-bottom: 5px;
  margin-top: 10px;
}

#main-links {
  text-align: left;
  text-decoration: none;
  display: block;
  padding-bottom: 4px;
  min-height: max-content;
  height: 46px;
}

a {
  text-decoration: none;
  color: black;
  transform: scale(1.1);
}

ul#component-links {
  margin: auto;
  padding: 0;
}

#component-links > li {
  display: inline-block;
  padding: 5px 10px 10px 10px;
  font-size: 1.2em;
}

.component-link {
  box-sizing: border-box;
  color: rgb(11, 10, 10);
  box-shadow: 0px 2px 5px 2px rgb(80, 79, 79);
  text-transform: uppercase;
  text-decoration: none;
  background: #cfd5d7;
  padding: 4px 16px;
  border-radius: 12px;
  // display: inline-block;
  border: none;
  transition: ease-in-out;
}

.component-link:hover {
  background: #484848;
  color: white;
  transform: scale(1.05);
  text-decoration: none;
  -webkit-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  -moz-box-shadow: 0px 5px 40px -10px rgba(0, 0, 0, 0.57);
  box-shadow: 0px 2px 5px 2px rgba(34, 34, 34, 0.57);
  // transition: all 0.2s ease 0s;
}

#date {
  color: black;
  display: inline;
  text-align: center;
  font-size: 1.5em;
}

#choose {
  font-size: 2em;
  margin-bottom: 5px;
  text-align: center;
  font-weight: bold;
}

h3 {
  font-size: 1em;
  color: black;
}

h4 {
  display: inline;
  color: black;
  font-size: 1.8em;
}
